.Detail-icon-detail {
  display: flex;
  margin-right: 20px;
  flex-basis: 40%;

  font-size: 14px;
  font-weight: 400;
  color: #343a40;

  .content {
    display: flex;
    align-items: center;
    text-align: center;
    vertical-align: middle;

    .icon {
      padding: 0 10px 0 0;
    }

    .phrases {
      white-space: nowrap;
    }
  }
}